.footer {
  display: flex;
  justify-content: flex-end;
  padding: 32px 104px 26px;
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 400;
}

.footer__text {
  margin: 0;
  margin-right: auto;
  align-self: center;
  font-size: 16px;
  line-height: 1.3;
  color: rgb(182, 188, 191);
}

.footer__links {
  display: flex;
  align-items: center;
}

.footer__link {
  text-decoration: none;
  font-size: 18px;
  line-height: 1.3;
  color: rgb(26, 27, 34);
  margin-right: 40px;
}

.footer__icons {
  display: flex;
  margin: 0;
  padding: 0;
  gap: 26px;
}

.footer__icon-item {
  list-style-type: none;
}

.footer__icon {
  width: 24px;
  height: 24px;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .footer {
    padding: 24px 40px 18px;
  }
}

@media screen and (min-width: 320px) and (max-width: 466px) {
  .footer {
    padding: 20px 16px 22px;
    flex-direction: column-reverse;
  }
  .footer__links {
    width: 100%;
    justify-content: space-between;
  }

  .footer__icons {
    margin-left: 233px;
    margin-right: 16px;
    /* display: flex;
    flex-direction: column;
    gap: 24px; */
  }

  .footer__text {
    margin: 40px 0 0 0;
  }
}
